import React from 'react';
import PropTypes from 'prop-types';

import './index.css';

export default class CheckSheet extends React.Component {
  static propTypes = {
    heading: PropTypes.string.isRequired,
    options: PropTypes.array.isRequired,
    onSubmit: PropTypes.func.isRequired,
    selectedValues: PropTypes.array.isRequired,
  }

  constructor(props) {
    super(props);

    this.state = {
      visible: false,
    };

    this.handleInputChange = this.handleInputChange.bind(this);
    this.onSubmit = this.onSubmit.bind(this);
  }

  componentWillReceiveProps(props) {
    const state = {
      visible: props.visible,
    };
    props.options.forEach(option => state[option.key] = false);
    props.selectedValues.forEach(value => state[value] = true);
    this.setState(state);
  }

  onSubmit(e) {
    e.preventDefault();

    const visible = !this.state.visible;
    if (!visible) {
      const locations = Object.entries(this.state)
        .filter(([name, val]) => val && name !== 'visible')
        .map(([name, val]) => name)
      this.props.onSubmit(locations);
    }

    this.setState({
      visible,
    });
  }

  handleInputChange(event) {
    const target = event.target;
    const name = target.name;
    const value = target.checked;

    this.setState({
      [name]: value
    });
  }

  render() {
    const {
      heading,
      options,
    } = this.props;
    const containerClassName = this.state.visible ? '' : 'CheckSheet--hidden';
    return (
      <div className={'CheckSheet'}>
        <div className={'clearfix'}>
          <div className={'left'}>
            <div className={'CheckSheet-heading'}>{heading}</div>
          </div>
          <div className={'right'}>
            <button onClick={this.onSubmit}>
              {this.state.visible ? 'Update' : 'Select Locations'}
            </button>
          </div>
        </div>
        <div className={`CheckSheet-container ${containerClassName}`}>
          {options.map(option => (
            <label key={option.key}>
              <input
                checked={this.state[option.key]}
                name={option.key}
                onChange={this.handleInputChange}
                type={'checkbox'} />
              {option.key}
            </label>
          ))}
        </div>
      </div>
    );
  }
}

